}

.movie__card {
  margin-bottom: 20px;
}

.movie__card:last-child {
  margin-bottom: 0;
}

@media screen and (min-width: 768px) {
  .movie__card {
    flex-basis: calc(50% - 16px);
    margin-bottom: 0;
  }
}

@media screen and (min-width: 1280px) {
  .movie__card {
