.movie__card {
  cursor: pointer;
  padding-bottom: 10px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.movie__card:hover, .movie__card:focus {
  transform: scale(1.03);
  box-shadow: 1px 4px 6px rgba(0, 0, 0, .16), 0 4px 4px rgba(0, 0, 0, .06), 0 1px 1px rgba(0, 0, 0, .12);
}

.movie__poster {
