}

@media screen and (min-width: 1280px) {
  .movie__poster {
    width: 100%;
    height: 574px;
    margin-bottom: 12px;
  }
}

.movie__info {
  padding-left: 10px;
  padding-right: 10px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

@media screen and (min-width: 1280px) {
