    margin-left: 10px;
  }
}

.movie__rating {
  background-color: var(--active-color);
  color: var(--bgBasic);
  border-radius: 5px;
  padding: 2px 10px;
  font-size: 20px;
  font-weight: 500;
  position: absolute;
  top: 10px;
  right: 10px;
}

