.pagination {
  justify-content: center;
  margin-top: 40px;
  margin-bottom: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .pagination {
    margin-top: 60px;
    margin-bottom: 60px;
  }
}

.pagination__wrapper {
  flex-wrap: wrap;
  display: flex;
}

.pagination__button {
  width: 40px;
  height: 40px;
  border: 1px solid var(--bgBasic);
  color: var(--btn-text-color);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin: 0;
  padding: 0;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.333;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.pagination__button.pagination__button--prev, .pagination__button.pagination__button--next {
  background-color: var(--footer-bg);
  stroke: currentColor;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pagination__button.pagination__button--current {
  background-color: var(--active-color);
  color: #fff;
}

.pagination__button.pagination__button:not(:disabled):hover, .pagination__button.pagination__button:not(:disabled):focus {
  background-color: var(--active-color);
  color: #fff;
  outline: none;
}

.pagination__button.pagination__button:disabled {
  background-color: var(--btn-disable-bg-color);
  color: var(--btn-disable-text-color);
  cursor: default;
}

